
.clear-left{
  clear:left;
}

.hero-image {
  position: relative;
  height: 50vh;
}
.hero-image::after {
  display: block;
  position: absolute;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0, #fff 100%);
  bottom: 0;
  left:0;
  right:0;
  height: 50%;
  width: 100%;
  content: '';
}
